// File: src/styles/gallery.scss
$app-gradient: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
$card-bg: #fff;
$accent: #0078ff;
$text-main: #333;
$text-secondary: #555;
$overlay-bg: rgba(0, 0, 0, 0.6);

.app {
    background: $app-gradient;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: sans-serif;
}

.header {
    background: transparent;
    padding: 24px;
    text-align: center;
    h1 {
        margin: 0;
        color: $accent;
        font-size: 28px;
        font-weight: 600;
        text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
    }
}

.gallery {
    padding: 20px;
    column-count: 4;
    column-gap: 16px;
    @media (max-width: 1200px) {
        column-count: 3;
    }
    @media (max-width: 800px) {
        column-count: 2;
    }
    @media (max-width: 500px) {
        column-count: 1;
    }
}

.image-card {
    display: inline-block;
    width: 100%;
    margin-bottom: 16px;
    background: $card-bg;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition:
        transform 0.2s ease,
        box-shadow 0.2s ease;
    &:hover {
        transform: translateY(-6px);
        box-shadow: 0 6px 18px rgba(0, 0, 0, 0.15);
    }

    &.wide .thumb {
        aspect-ratio: 4 / 3;
    }
    &.tall .thumb {
        aspect-ratio: 3 / 4;
    }

    .thumb {
        width: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .info {
        padding: 10px;
        .caption {
            font-size: 15px;
            color: $text-main;
            margin: 0 0 6px;
        }
        .wish {
            font-size: 13px;
            color: $text-secondary;
            margin: 0;
        }
    }
}

// Модальное окно
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $overlay-bg;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
.modal-content {
    background: $card-bg;
    border-radius: 10px;
    max-width: 90%;
    max-height: 90%;
    overflow: hidden;
    position: relative;

    img {
        display: block;
        max-width: 100%;
        max-height: 70vh;
        margin: auto;
    }
}
.close-btn {
    position: absolute;
    top: 10px;
    right: 12px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
}
.modal-info {
    padding: 12px;
    text-align: center;
    h3 {
        margin: 0 0 8px;
        color: $text-main;
    }
    p {
        margin: 0;
        color: $text-secondary;
    }
}
